footer {
  background-color: var(  --background-primary);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}

footer a {
  color: var(--color-black);
}

.footer__menu {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.footer__menu li {
  padding: 0 1rem;
}

.footer__logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}

.footer__links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 3rem 0 0;
}

.footer__socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
}

.footer__socials a {
  background: var(--color-bg);
  color: var(--color-black);
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
  font-size: 1.5rem;
}

.footer__socials a:hover {
  background: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}

.footer__copyright {
  margin-bottom: 4rem;
  color: var(--color-black);
}

@media screen and (max-width: 600px) {

    .footer__menu {
        flex-direction: column;
        gap: 1.5rem;
    }
   
    .footer__socials {
        margin-bottom: 2.6rem;
    }


}
