header {
    width: 100%;
    height: 100vh;
}

.header__container {
    width: 100%;
    height: 100%;
}
    
    
video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    
}


.content {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}



