.experience__frontend {
    padding: 2rem;
  border-radius: 2rem;
}
.experience{
    text-align: center;
}


.experience__details {
  min-width: 160px;
  margin-top:1.5rem
}

.experience__details h4 {
    font-size:1.3rem;
    
}

.experience__details-icon {
    font-size: 2rem;
    cursor: pointer;
    
}

.experience__details-icon:hover {
    color: var(--color-primary-hover);
}


.group__up {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

}

.group__down {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

}



