.about-section {
  text-align: center;
  justify-content: center;
}



.about {
  grid-template-columns: repeat(2, 1fr);
  display: grid;
  column-gap: 4rem;
  align-items: center;
}

.about img {
  width: 480px;
  border-radius: 0.7rem;
  justify-self: center;
}

.about-title {
  font-family: var(--font-alt);
  font-size: 3.5rem;
  color: var(--color-primary);
  margin-bottom: 2rem;
}

.about-description {
  font-size: 1rem;
  color:var(--color-primary);
}

.about-description h3 {
  font-size: 2rem;
  font-family: var(--font-alt);
  margin-bottom: 1.9rem;
}

.about-description p {
  font-size: 1.5rem;
  font-family: var(--font-base);
  margin-bottom: 2rem;
}

.about-description button {
  font-size: 1.25rem;
  font-family: var(--font-base);
  font-weight: 500;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #fff;
  background: #000;
  color: var(--color-btn);
  cursor: pointer;
}

.about-description button:hover {
  border: 1px solid #fff;
  background: var(--color-btn-hover);
  color: var(--color-primary-hover);
  cursor: pointer;
}

.cta {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}



@media screen and (max-width: 1150px) {
  .about {
    grid-template-columns: repeat(1, 1fr);
    display: grid;
    column-gap: 3rem;
    align-items: center;
  }
  .about-image {
    margin-bottom: 2rem;
  }

  .about-image img {
    width: 100%;
  }
}

@media screen and (max-width: 650px) {


  .section__padding {
    padding: 4rem 1.5rem
  }
}
