/* fonts */
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital@0;1&family=Space+Grotesk:wght@500;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  /* font-family*/

  --font-base: "Source Sans Pro", sans-serif;
  --font-title: "Space Grotesk", sans-serif;

  /* transition */

  --transition: all 400ms ease;

 

  /* colors */
  
  --color-primary: #fff;
  --color-primary-hover: #dabc53;
  --color-dark: #000;
  --color-btn: rgb(12,12,14);
  --color-btn-hover: linear-gradient(166deg, rgba(92,91,98,1) 3%, rgba(61,56,56,1) 99%);
  --background-primary: rgba(92,91,98,0.3) ;
  --background-primary-hover: rbga(12,12,14, 0.3);

  /* sizing */

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%%;

}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: var(--font-base);
  background: var(--color-bg);
  color: var(--color-text);
  line-height: 1.7;
}

/* GENERAL STYLES */

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

.section__padding {
  padding: 6rem 3rem
}
.flex__center {
  display: flex;
  justify-content: center;
  align-items: center;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}



h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.5rem;
  color: var(--color-primary-hover);
  margin-bottom: 2rem;
}

h3 {
  font-size: 1.6rem;
  color: var(--color-btn-hover);
  margin-bottom: 2rem;
}

h5 {
  font-size: 1.5rem;
  color: var(--color-btn-hover);
}

section {
  margin-top: 8rem;
  text-align: center;
}



.font-base {
  font-family: var(--font-base);
}

.text-light {
  color: var(--color-primary);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-primary-hover);
}

.slide-bottom {
  -webkit-animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

.btn {
  width: max-content;
  display: inline-block;
  background: var(--color-btn);
  color: var(--color-primary);
  padding: 0.75 1.2rem;
  border-radius: 16px;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
  padding: .75rem 1rem;
}
 

.btn:hover {
  background: var( --color-btn-hover);
  color: var(--color-primary);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-black);
  color: var(--color-white);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */

@media screen and (max-wdith: 1024px) {
  .container {
    width: var(--container-width-md);
  }

  section {
    margin-top: 4rem;
  }
}

/* MEDIA QUERIES (SMALL DEVICES) */

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }

  
h2 {
  font-size: 2rem;
  color: var(--color-primary-hover);
  margin-bottom: 2rem;
}

h3 {
  font-size: 1.2rem;
  color: var(--color-btn-hover);
  margin-bottom: 2rem;
}

h5 {
  font-size: 1.1rem;
  color: var(--color-btn-hover);
}


}
