.navbar {
    position: fixed;
    height: 90px;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    transition: .3s ease-in;
    overflow: hidden;
}

.navbar__bg {
    background-color: rgb(0,0,0,.9);
}

.app__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1240px;
    margin: auto;
    height: 100%;
    padding: 0 1rem;
}

.app__navbar-logo {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: 80px;
}

.app__navbar-logo img {
    color: var(--color-primary);
    cursor: pointer;
}


.app__navbar-links {
    display: flex;
    justify-content: center;
    align-items: center;
    
    list-style: none;
}

.app__navbar-links li {
    margin: 0 1rem;
    cursor: pointer;
}

.app__navbar-links li:hover {
    color: var(  --color-primary-hover);
}

.app__navbar-smallscreen {
    display: none;
}


.app__navbar-smallscreen_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,.9);
    transition: .5s ease;

    flex-direction: column;
    z-index: 5;
}

.app__navbar-smallscreen_overlay .overlay__close {
    font-size: 27px;
    margin: 1rem 0;
    color: var(--color-primary);
    cursor: pointer;

    position: absolute;
    top: 20px;
    right: 20px;
}

.app__navbar-smallscreen_links {
    
    list-style: none;
}

.app__navbar-smallscreen_links li {
    margin: 2rem;
    cursor: pointer;
    color: var(--color-primary);
    font-size: 2rem;
    text-align: center;
    font-family: var(--font-base);
    
}

.app__navbar-smallscreen_links li:hover {
    color: var(--color-primary-hover);
}

@media screen and (min-width: 2000px) {
    .app__navbar-logo img {
        width: 210px;
    }
}

@media screen and (max-width: 1150px) {
    .app__navbar-links {
        display: none;
    }

    .app__navbar-smallscreen {
        display: flex;
    }
}

@media screen and (max-width: 650px) {
    .app__navbar {
        padding: 1rem;
    }

    .app__navbar-smallscreen li {
        font-size:1.5rem;
    }
    .app__navbar-logo img {
        width: 75px;
    }
}
